@import url(https://fonts.googleapis.com/css?family=Roboto:400,500,700&display=swap);
* {
    margin: 0;
    padding: 0;
    outline: 0;
    box-sizing: border-box;
}

body {
    font: 400 14px Roboto, sans-serif;
    background: #f0f0f5;
    -webkit-font-smoothing: antialisead;
}

input, button, textarea {
    font: 400 14px Roboto, sans-serif;
}

button {
    cursor: pointer;
}

form input {
    width: 100%;
    height: 60px;
    color: #333;
    border: 1px solid #dcdce6;
    border-radius: 8px;
    padding: 0 24px;
}

form textarea{
    width: 100%;
    resize: vertical;
    min-height: 140px;
    height: 60px;
    color: #333;
    border: 1px solid #dcdce6;
    border-radius: 8px;
    padding: 16px 24px;
    line-height: 24px;
}


.button {
    width: 100%;
    height: 60px;
    background: #F26E22;
    border: 0;
    border-radius: 8px;
    color: #FFF;
    font-weight: bold;
    margin-top: 16px;
    display: inline-block;
    text-align: center;
    text-decoration: none;
    font-size: 18px;
    line-height: 60px;
    transition: -webkit-filter 0.2s;
    transition: filter 0.2s;
    transition: filter 0.2s, -webkit-filter 0.2s;
}

.button:hover{
    -webkit-filter: brightness(90%);
            filter: brightness(90%);
}

.back-link {
    display: flex;
    align-items: center;
    margin-top: 25px;
    color: #41414d;
    font-size: 18px;
    text-decoration: none;
    font-weight: 500;
    transition: opacity 0.2s;
}

.back-link svg {
    margin-right: 8px;
}

.back-link:hover{
    opacity: 0.8;
}
.register-container {
    width: 100%;
    max-width: 1120px;
    height: 100vh;
    margin: 0 auto;

    display: flex;
    align-items: center;
    justify-content: center;
}

.register-container header {
    display: flex;
}

.register-container header button{
    height: 60px;
    width: 60px;
    border-radius: 4px;
    border: 1px solid;
    background: transparent;
    margin-top: -255px;
    transition: border-color 0.2s;
}

.register-container .content{
width: 100%;
padding: 96px;
background: #f0f0f5;
box-shadow: 0 0 100px rgba(0,0,0,0.1);
border-radius: 8px;

display: flex;
justify-content: space-between;
align-items: center;
}

.register-container .content section{
    width: 100%;
    max-width: 380px;
}

.register-container .content section h1{
    margin: 64px 0 32px;
    font-size: 32px;
}

.register-container .content section p {
    font-size: 18px;
    color: #737380;
    line-height: 32px;
}

.register-container .content form {
    width: 100%;
    max-width: 450px;

}

.register-container .content form input {
    margin-top: 8px;
}

.register-container .content form textarea {
    margin-top: 8px;
}

.login-container {
    width: 100%;
    max-width: 1120px;
    height: 100vh;
    margin: 0 auto;

    display: flex;
    align-items: center;
    justify-content: space-between;
}

.login-container section.form{
    width: 100%;
    max-width: 350px;
    margin-right: 30px;
}

.login-container section.form form{
    margin-top: 100px;
}

.login-container section.form form h1{
    font-size: 32px;
    margin-bottom: 32px;
}


.product-container {
    width: 100%;
    max-width: 1120px;
    height: 100vh;
    margin: 0 auto;

    display: flex;
    align-items: center;
    justify-content: center;
}

.product-container .content{
width: 100%;
padding: 96px;
background: #f0f0f5;
box-shadow: 0 0 100px rgba(0,0,0,0.1);
border-radius: 8px;

display: flex;
justify-content: space-between;
align-items: center;
}
.product-container header {
    display: flex;
}
.product-container header button{
    height: 60px;
    width: 60px;
    border-radius: 4px;
    border: 1px solid;
    background: transparent;
    margin-top: -255px;
    transition: border-color 0.2s;
}

.product-container .content section{
    width: 100%;
    max-width: 380px;
}

.product-container .content section h1{
    margin: 64px 0 32px;
    font-size: 32px;
}

.product-container .content section p{
    font-size: 18px;
    color: #737380;
    line-height: 32px;
}

.product-container .content form {
    width: 100%;
    max-width: 450px;
    

}

.product-container .content form input {
    margin-top: 8px;
    background-color: #ffff;
}

.product-container .content form .input-group{
    display: flex;
}

.product-container .content form .input-group input + input{
    margin-left: 8px;
}
.AllProducts-container {
    width: 100%;
    max-width: 1180px;
    padding: 30px;
    margin: 32px auto;
}

.AllProducts-container header {
    display: flex;
    align-items: center;
}

.AllProducts-container header span{
    font-size: 20px;
    margin-left: 24px;
}

.AllProducts-container header img{
    height: 64px;
}

.AllProducts-container header a{
    width: 260px;
    margin-left: auto;
    margin-top: 0;
}

.AllProducts-container header button{
    height: 60px;
    width: 60px;
    border-radius: 4px;
    border: 1px solid;
    background: transparent;
    margin-left: 16px;
    transition: border-color 0.2s;
}

.AllProducts-container header button:houver{
    border-color: #999;
}

.AllProducts-container h1{
    margin-top: 80px;
    margin-bottom: 24px;
}

.AllProducts-container ul {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 24px;
    list-style: none;
}

.AllProducts-container li {
    background: #fff;
    padding: 24px;
    border-radius: 8px;
    position: relative;
}

.AllProducts-container li button{
    position: absolute;
    right: 24px;
    top: 24px;
    border: 0;
}

.AllProducts-container li button:hover{
    opacity: 0.8;
}

.AllProducts-container ul li strong {
    display: block;
    margin-bottom: 16px;
    color: #41414d;
}
.AllProducts-container ul li img {
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.AllProducts-container ul li p + strong {
    margin-top: 15px;
}

.AllProducts-container ul li p {
    color: #737380;
    line-height: 21px;
    font-size: 16px;
}
